import React from "react"
import { MyBase } from "@macashipo/mlib"
import useConfigTableCell from "../../hooks/useConfigTableCell"
import styled from "styled-components"

const WrapCell = styled.div`
  display: flex;
  & > textarea {
    width: 100%;
    // min-height: 47px;
    min-height: 100%;
    border: 0px;
    outline: none;
    resize: none;
    background: transparent;
    overflow: overlay;
    padding: 0px;
    &:focus {
      background: var(--color-bg-focus);
    }
  }
`
export default function MTCTextarea(props) {
  const ctc = useConfigTableCell({ props })
  // console.warn("render MTCTextarea:", props, ctc)
  return (
    <WrapCell
      className={ctc.classNameWrap("mtc-textareafull")}
      style={ctc.styleWrap()}
    >
      <MyBase
        type="textareafull"
        value={ctc.valueCell}
        onChange={v => {
          ctc.setValueCell(v)
        }}
        onBlur={v => {
          ctc.updateFieldData(v)
        }}
        attrs={{
          disabled: ctc.canEdit === true ? false : true,
        }}
      />
      {ctc.renderTopRight([], { ctc })}
    </WrapCell>
  )
}
